@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,900&family=Rancho&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
//font-family: 'Poppins', sans-serif;

//font-family: 'Rancho', cursive;
@import "preload";
@import "video";
@import "contact";
@import "footer";
@import "close";

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
  min-height: 100vh;
}
a {
  text-decoration: none;
  cursor: pointer;
}
li {
  list-style: none;
}
//---------------- section
.header {
  position: relative;
  width: 100%;
  padding: 30px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  position: relative;
  color: #222;
  font-size: 2em;
  font-weight: 700;
}
.group {
  position: relative;
  display: flex;
}
.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
}
.action-search {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 30px;
}
.group ul li a {
  color: #222;
  margin-left: 30px;
  font-size: 1.25em;
  font-weight: 400;
  transition: color 0.2s ease-in-out;
}
.group ul li a:hover {
  color: #ff578b;
}
.banner {
  position: relative;
  width: 100%;
  padding: 20px 100px;
}
.bg {
  position: relative;
  width: 100%;
  height: 75vh;
  background: #555;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.content-time {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.content-time h2 {
  position: relative;
  color: #fff;
  font-size: 5em;
}
.btn {
  display: inline-block;
  background: #ff5a26;
  color: #fff;
  padding: 20px 40px;
  font-size: 1.1em;
  margin-top: 20px;
  letter-spacing: 2px;
}
.btn:hover {
  box-shadow: -2px 2px 5px 0 #f503888c;
}
.searchBox {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background: #fff;
  padding: 30px 50px;
  transform: translateY(50%);
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
}
.searchBox__input {
  margin: 0 20px;
  min-width: 150px;
}
.searchBox__input p {
  color: #fff;
  font-size: 0;
}
.white {
  color: #fff;
}
.searchBox__input input {
  border: 1px solid #222;
  outline: none;
  padding: 10px;
  width: 100%;
  font-size: 16px;
}

.searchBox__input input[type="submit"] {
  background: #ff5a26;
  color: #fff;
  border: none;
  outline: none;
  font-size: 1.1em;
  font-weight: 500;
  cursor: pointer;
  letter-spacing: 2px;
  padding: 10px;
}
.searchBox__input input[type="submit"]:hover {
  box-shadow: -1px 1px 3px 0#f503888c;
}
@media (max-width: 1110px) {
  .banner {
    padding: 20px 40px 80px;
  }
  .header {
    padding: 20px 40px;
  }
  .searchBox {
    padding: 30px 30px;
  }
  .searchBox__input {
    min-width: 140px;
  }
}
@media (max-width: 991px) {
  .navigation {
    display: none;
  }
  .action-search {
    margin-right: 100px;
  }
  .searchBox__input {
    margin: 0 10px;
  }
  .searchBox {
    padding: 20px 10px;
  }
  .content-time h2 {
    font-size: 3em;
  }
}

@media (max-width: 840px) {
  .searchBox {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    gap: 15px;
    padding: 15px 20px;
  }
  .btn {
    padding: 10px 30px;
  }
  .searchBox__input {
    margin: 0;
  }
}
@media (max-width: 540px) {
  .banner {
    padding: 15px 10px 80px;
  }
  .header {
    padding: 15px 10px;
  }
  .content-time h2 {
    font-size: 2em;
  }
  .searchBox {
    gap: 10px;
    padding: 10px 15px;
  }
}
@media (max-width: 420px) {
  .searchBox {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 25%;
    gap: 15px;
    padding: 15px 20px;
  }
}
