.container__video {
  position: relative;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  margin-top: 100px;
}

.container__video video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.container__video video:nth-child(2) {
  mix-blend-mode: screen;
  filter: brightness(2);
}

.contact__block {
  background: linear-gradient(90deg, #0e3959 0%, #0e3959 30%, #03a9f5 30%, #03a9f5 100%);
  min-height: 100vh;
  padding-bottom: 120px;
}

.contactUs {
  position: relative;
  width: 100%;
  padding: 40px 100px 100px;
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
}

.title h2 {
  color: #fff;
  font-weight: 500;
}

.form {
  grid-area: form;
}

.info {
  grid-area: info;
}

.map {
  grid-area: map;
}

.contact {
  padding: 40px;
  background: #fff;
  box-shadow: 0 5px 35px rgba(0, 0, 0, 0.15);
}

.contact h3 {
  color: #0e3959;
  font-weight: 500;
  font-size: 1.4em;
  margin-bottom: 10px;
}

.box {
  position: relative;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 5fr 4fr;
  grid-template-areas: "form info" "form map";
  grid-gap: 20px;
}

.formBox {
  position: relative;
  width: 100%;
}

.row50 {
  display: flex;
  gap: 20px;
}

.inputBox {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  width: 50%;
}

.row100 .inputBox {
  width: 100%;
}

.inputBox span {
  color: #18b7ff;
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: 500;
}

.inputBox input {
  padding: 10px;
  font-size: 1.1em;
  outline: none;
  border: 1px solid #333;
}

.inputBox textarea {
  padding: 10px;
  font-size: 1.1em;
  outline: none;
  border: 1px solid #333;
  width: 100%;
  resize: vertical;
  min-height: 320px;
  margin-bottom: 10px;
}

.inputBox input:hover,
.inputBox textarea:hover {
  box-shadow: -1px 1px 3px 0 #f503888c;
}

.inputBox input:active,
.inputBox textarea:active {
  box-shadow: -1px 1px 3px 0 #ff18ff9c;
}

.inputBox input:focus,
.inputBox textarea:focus {
  box-shadow: -1px 1px 3px 0 #ff18ffb0;
}

.inputBox input[type=submit] {
  background: #ff5a26;
  color: #fff;
  border: none;
  font-size: 1.1em;
  max-width: 120px;
  font-weight: 500;
  cursor: pointer;
  padding: 14px 15px;
}

.inputBox ::placeholder {
  color: #999;
}

.info {
  background: #0e3959;
}

.info h3 {
  color: #fff;
}

.infoBox__block {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.infoBox__block span {
  min-width: 40px;
  height: 40px;
  color: #fff;
  background: #18b7ff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  border-radius: 50%;
  margin-right: 15px;
}

.infoBox__block p {
  color: #fff;
  font-size: 1.1em;
}

.infoBox__block a {
  color: #fff;
  font-size: 1.1em;
}

.sci {
  margin-top: 40px;
  display: flex;
}

.sci li {
  margin-right: 20px;
}

.sci li a {
  color: #ccc;
  font-size: 2em;
  transition: 0.3s all ease-in-out;
}

.sci li a:hover {
  color: #fff;
}

.contact.map {
  padding: 0;
}

.map iframe {
  width: 100%;
  height: 100%;
  min-height: 260px;
  border: 0;
}

@media (max-width: 1040px) {
  .contactUs {
    padding: 40px 50px 50px;
  }
}
@media (max-width: 991px) {
  .contactUs {
    padding: 20px;
  }

  .contact {
    padding: 15px;
  }
}
@media (max-width: 840px) {
  .box {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-template-areas: "form" "info" "map";
  }

  .inputBox textarea {
    min-height: 220px;
  }
}
@media (max-width: 767px) {
  .formBox .row50 {
    display: flex;
    gap: 0;
    flex-direction: column;
  }

  .inputBox {
    width: 100%;
  }

  .title h2 {
    color: #fff;
    font-weight: 500;
    font-size: 1em;
  }

  .inputBox input {
    font-size: 1em;
  }

  .contact__block {
    background: #03a9f5;
  }

  .contactUs {
    padding: 20px 10px;
  }
}
.footer {
  position: relative;
  width: 100%;
  background: #3586ff;
  min-height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 50px;
  flex-direction: column;
  bottom: 0;
}

.social__icon,
.menu__footer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  flex-wrap: wrap;
}

.social__icon a {
  font-size: 2em;
  color: #fff;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s all ease-in-out;
}

.menu__footer a {
  font-size: 1.2em;
  color: #fff;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s all ease-in-out;
  opacity: 0.5;
}

.menu__footer a:hover {
  opacity: 1;
}

.social__icon a:hover {
  transform: translateY(-10px);
}

.footer p {
  color: #fff;
  margin-top: 15px;
  text-align: center;
  font-size: 0.9em;
  margin-bottom: 10px;
}

.wave {
  position: absolute;
  top: -100px;
  left: 0;
  width: 100%;
  height: 100px;
  background: url(../img/wave.webp);
  background-size: 1000px 100px;
}

#wave1 {
  z-index: 1000;
  opacity: 1;
  bottom: 0;
  animation: animateWave 4s linear infinite;
}

@keyframes animateWave {
  0% {
    background-position-x: 1000px;
  }
  100% {
    background-position-x: 0px;
  }
}
#wave2 {
  z-index: 997;
  opacity: 0.5;
  bottom: 10px;
  animation: animateWave_02 4s linear infinite;
}

#wave3 {
  z-index: 996;
  opacity: 0.4;
  bottom: 25px;
  animation: animateWave_02 5s linear infinite;
}

#wave4 {
  z-index: 998;
  opacity: 0.7;
  bottom: 20px;
  animation: animateWave_02 3s linear infinite;
}

@keyframes animateWave_02 {
  0% {
    background-position-x: 0px;
  }
  100% {
    background-position-x: 1000px;
  }
}
.toggle {
  display: none;
}

@media (max-width: 991px) {
  .toggle {
    display: block;
    border: none;
    position: fixed;
    top: 28px;
    right: 40px;
    width: 30px;
    height: 30px;
    cursor: pointer;
    background: url(../img/menu.webp);
    filter: invert(1);
    background-size: 30px;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 10000;
  }

  .toggle.active {
    position: fixed;
    top: 30px;
    right: 40px;
    background: url(../img/close.webp);
    filter: invert(1);
    width: 25px;
    height: 25px;
    background-size: 25px;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 10001;
  }

  .navigation.active {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    z-index: 10000;
    background: #fff;
  }

  .navigation.active li {
    margin-left: 0;
  }

  .navigation.active li a {
    font-size: 1.8em;
    margin: 5px 0;
    display: inline-block;
    border-radius: 30px;
  }
}
@media (max-width: 540px) {
  .toggle.active,
.toggle {
    top: 25px;
    right: 20px;
  }
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
  min-height: 100vh;
}

a {
  text-decoration: none;
  cursor: pointer;
}

li {
  list-style: none;
}

.header {
  position: relative;
  width: 100%;
  padding: 30px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  position: relative;
  color: #222;
  font-size: 2em;
  font-weight: 700;
}

.group {
  position: relative;
  display: flex;
}

.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
}

.action-search {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 30px;
}

.group ul li a {
  color: #222;
  margin-left: 30px;
  font-size: 1.25em;
  font-weight: 400;
  transition: color 0.2s ease-in-out;
}

.group ul li a:hover {
  color: #ff578b;
}

.banner {
  position: relative;
  width: 100%;
  padding: 20px 100px;
}

.bg {
  position: relative;
  width: 100%;
  height: 75vh;
  background: #555;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.content-time {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.content-time h2 {
  position: relative;
  color: #fff;
  font-size: 5em;
}

.btn {
  display: inline-block;
  background: #ff5a26;
  color: #fff;
  padding: 20px 40px;
  font-size: 1.1em;
  margin-top: 20px;
  letter-spacing: 2px;
}

.btn:hover {
  box-shadow: -2px 2px 5px 0 #f503888c;
}

.searchBox {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background: #fff;
  padding: 30px 50px;
  transform: translateY(50%);
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
}

.searchBox__input {
  margin: 0 20px;
  min-width: 150px;
}

.searchBox__input p {
  color: #fff;
  font-size: 0;
}

.white {
  color: #fff;
}

.searchBox__input input {
  border: 1px solid #222;
  outline: none;
  padding: 10px;
  width: 100%;
  font-size: 16px;
}

.searchBox__input input[type=submit] {
  background: #ff5a26;
  color: #fff;
  border: none;
  outline: none;
  font-size: 1.1em;
  font-weight: 500;
  cursor: pointer;
  letter-spacing: 2px;
  padding: 10px;
}

.searchBox__input input[type=submit]:hover {
  box-shadow: -1px 1px 3px 0 #f503888c;
}

@media (max-width: 1110px) {
  .banner {
    padding: 20px 40px 80px;
  }

  .header {
    padding: 20px 40px;
  }

  .searchBox {
    padding: 30px 30px;
  }

  .searchBox__input {
    min-width: 140px;
  }
}
@media (max-width: 991px) {
  .navigation {
    display: none;
  }

  .action-search {
    margin-right: 100px;
  }

  .searchBox__input {
    margin: 0 10px;
  }

  .searchBox {
    padding: 20px 10px;
  }

  .content-time h2 {
    font-size: 3em;
  }
}
@media (max-width: 840px) {
  .searchBox {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    gap: 15px;
    padding: 15px 20px;
  }

  .btn {
    padding: 10px 30px;
  }

  .searchBox__input {
    margin: 0;
  }
}
@media (max-width: 540px) {
  .banner {
    padding: 15px 10px 80px;
  }

  .header {
    padding: 15px 10px;
  }

  .content-time h2 {
    font-size: 2em;
  }

  .searchBox {
    gap: 10px;
    padding: 10px 15px;
  }
}
@media (max-width: 420px) {
  .searchBox {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 25%;
    gap: 15px;
    padding: 15px 20px;
  }
}