.contact__block {
  background: linear-gradient(
    90deg,
    #0e3959 0%,
    #0e3959 30%,
    #03a9f5 30%,
    #03a9f5 100%
  );
  min-height: 100vh;
  padding-bottom: 120px;
}
.contactUs {
  position: relative;
  width: 100%;
  padding: 40px 100px 100px;
}
.title {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
}
.title h2 {
  color: #fff;
  font-weight: 500;
}
.form {
  grid-area: form;
}
.info {
  grid-area: info;
}
.map {
  grid-area: map;
}
.contact {
  padding: 40px;
  background: #fff;
  box-shadow: 0 5px 35px rgba(0, 0, 0, 0.15);
}
.contact h3 {
  color: #0e3959;
  font-weight: 500;
  font-size: 1.4em;
  margin-bottom: 10px;
}
.box {
  position: relative;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 5fr 4fr;
  grid-template-areas:
    "form info"
    "form map";
  grid-gap: 20px;
}
.formBox {
  position: relative;
  width: 100%;
}
.row50 {
  display: flex;
  gap: 20px;
}
.inputBox {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  width: 50%;
}
.row100 .inputBox {
  width: 100%;
}
.inputBox span {
  color: #18b7ff;
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: 500;
}
.inputBox input {
  padding: 10px;
  font-size: 1.1em;
  outline: none;
  border: 1px solid #333;
}
.inputBox textarea {
  padding: 10px;
  font-size: 1.1em;
  outline: none;
  border: 1px solid #333;
  width: 100%;
  resize: vertical;
  min-height: 320px;
  margin-bottom: 10px;
}
.inputBox input:hover,
.inputBox textarea:hover {
  box-shadow: -1px 1px 3px 0#f503888c;
}
.inputBox input:active,
.inputBox textarea:active {
  box-shadow: -1px 1px 3px 0#ff18ff9c;
}
.inputBox input:focus,
.inputBox textarea:focus {
  box-shadow: -1px 1px 3px 0#ff18ffb0;
}
.inputBox input[type="submit"] {
  background: #ff5a26;
  color: #fff;
  border: none;
  font-size: 1.1em;
  max-width: 120px;
  font-weight: 500;
  cursor: pointer;
  padding: 14px 15px;
}
.inputBox ::placeholder {
  color: #999;
}
.info {
  background: #0e3959;
}
.info h3 {
  color: #fff;
}
.infoBox__block {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.infoBox__block span {
  min-width: 40px;
  height: 40px;
  color: #fff;
  background: #18b7ff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  border-radius: 50%;
  margin-right: 15px;
}
.infoBox__block p {
  color: #fff;
  font-size: 1.1em;
}
.infoBox__block a {
  color: #fff;
  font-size: 1.1em;
}
.sci {
  margin-top: 40px;
  display: flex;
}
.sci li {
  margin-right: 20px;
}
.sci li a {
  color: #ccc;
  font-size: 2em;
  transition: 0.3s all ease-in-out;
}
.sci li a:hover {
  color: #fff;
}
.contact.map {
  padding: 0;
}
.map iframe {
  width: 100%;
  height: 100%;
  min-height: 260px;
  border: 0;
}
@media (max-width: 1040px) {
  .contactUs {
    padding: 40px 50px 50px;
  }
}
@media (max-width: 991px) {
  .contactUs {
    padding: 20px;
  }
  .contact {
    padding: 15px;
  }
}
@media (max-width: 840px) {
  .box {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-template-areas:
      "form"
      "info"
      "map";
  }
  .inputBox textarea {
    min-height: 220px;
  }
}
@media (max-width: 767px) {
  .formBox .row50 {
    display: flex;
    gap: 0;
    flex-direction: column;
  }
  .inputBox {
    width: 100%;
  }
  .title h2 {
    color: #fff;
    font-weight: 500;
    font-size: 1em;
  }
  .inputBox input {
    font-size: 1em;
  }
  .contact__block {
    background: #03a9f5;
  }
  .contactUs {
    padding: 20px 10px;
  }
}
