.container__video {
  position: relative;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  margin-top: 100px;
}
.container__video video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.container__video video:nth-child(2) {
  mix-blend-mode: screen;
  filter: brightness(2);
}
