.footer {
  position: relative;
  width: 100%;
  background: #3586ff;
  min-height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 50px;
  flex-direction: column;
  bottom: 0;
}

.social__icon,
.menu__footer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  flex-wrap: wrap;
}
.social__icon a {
  font-size: 2em;
  color: #fff;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s all ease-in-out;
}
.menu__footer a {
  font-size: 1.2em;
  color: #fff;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s all ease-in-out;
  opacity: 0.5;
}
.menu__footer a:hover {
  opacity: 1;
}
.social__icon a:hover {
  transform: translateY(-10px);
}
.footer p {
  color: #fff;
  margin-top: 15px;
  text-align: center;
  font-size: 0.9em;
  margin-bottom: 10px;
}
.wave {
  position: absolute;
  top: -100px;
  left: 0;
  width: 100%;
  height: 100px;
  background: url(../img/wave.webp);
  background-size: 1000px 100px;
}
#wave1 {
  z-index: 1000;
  opacity: 1;
  bottom: 0;
  animation: animateWave 4s linear infinite;
}
@keyframes animateWave {
  0% {
    background-position-x: 1000px;
  }
  100% {
    background-position-x: 0px;
  }
}
#wave2 {
  z-index: 997;
  opacity: 0.5;
  bottom: 10px;
  animation: animateWave_02 4s linear infinite;
}

#wave3 {
  z-index: 996;
  opacity: 0.4;
  bottom: 25px;
  animation: animateWave_02 5s linear infinite;
}

#wave4 {
  z-index: 998;
  opacity: 0.7;
  bottom: 20px;
  animation: animateWave_02 3s linear infinite;
}
@keyframes animateWave_02 {
  0% {
    background-position-x: 0px;
  }
  100% {
    background-position-x: 1000px;
  }
}
